import { writable } from "svelte/store";
import { AdsError } from "../../../shared/errors/ads-error";
import { ReinitError } from "../../../shared/errors/reinit-error";
import { TerminateError } from "../../../shared/errors/terminate-error";

export type errorStoreTypes_Errors =
  | AdsError
  | ReinitError
  | TerminateError
  | null;
export type errorStoreTypes = errorStoreTypes_Errors | null;
const error_store: errorStoreTypes = null;

/**
 * Хранит информацию об ошибках возникших
 * на любом этапе работы плеера
 */

export const errorStore = writable(error_store as errorStoreTypes);
