import type { playerInitParamsOutstream } from "../Interfaces/player-init-params";

interface cssVarsI {
  "color--icon": string;
  "color--add": string;
  "collor--bg": string;
  "color--stroke": string;
  "color--line": string;
}
/**
 * Создает CSS переменные для вставки в Svelt inline styles
 */
export const getStyleVar = (config: playerInitParamsOutstream): cssVarsI => {
  const configVars = {
    "color--line": config.lineColor,
  };
  switch (config.skin) {
    case "dark":
      return {
        ...configVars,
        "color--icon": "rgb(236, 236, 236)",
        "color--add": "#fff",
        "collor--bg": "rgba(0, 0, 0, 0.53);",
        "color--stroke": "#fff",
      } as cssVarsI;
    case "white":
      return {
        ...configVars,
        "color--icon": "rgb(46, 46, 46)",
        "color--add": "rgb(109, 109, 109)",
        "collor--bg": "rgb(236, 236, 236)",
        "color--stroke": "#000",
      } as cssVarsI;
    default:
      return {
        ...configVars,
        "color--icon": "rgb(236, 236, 236)",
        "color--add": "#fff",
        "collor--bg": "rgba(0, 0, 0, 0.53);",
        "color--stroke": "#fff",
      } as cssVarsI;
  }
};
