import type { VastMediaFile } from "vast-client";
import type { videoSourceT } from "../types/video-source";
import type { playerInitParamsOutstream } from "./../../outstream/src/Interfaces/player-init-params";

/**
 * MIME типы для видео
 */
const videoMimeTypes = [
  "video/mp4",
  "video/x-flv",
  "video/mpeg",
  "video/ogg",
  "video/quicktime",
  "video/webm",
];

const isVideoMimeType = (chekingType: string) =>
  videoMimeTypes.some((type) => type === chekingType);
/**
 * Конфигурирует обьект для инициализации видео тега
 */
export const configCreativesForVideo = (
  mediaFiles: VastMediaFile[],
  MEDIA_QUERYS: playerInitParamsOutstream["media_querys"]
): videoSourceT[] => {
  if (
    mediaFiles?.length === 1 &&
    isVideoMimeType(String(mediaFiles[0].mimeType))
  ) {
    return [
      {
        src: mediaFiles[0].fileURL,
        type: mediaFiles[0].mimeType,
        media: "all",
        poster: "",
      },
    ];
  }
  const sortedMedia = mediaFiles?.sort((a, b) => {
    return a.width < b.width ? -1 : 1;
  });

  return sortedMedia?.reduce((creatives, creative, index) => {
    if (isVideoMimeType(String(creative.mimeType)))
      creatives.push({
        src: creative.fileURL,
        type: creative.mimeType,
        media: MEDIA_QUERYS[index],
        poster: "",
      });
    return creatives;
  }, [] as videoSourceT[]);
};
