import type { VASTTracker } from "vast-client";
import { isVisible } from "../../../shared/support/is-visible";

let isImpressed = false;
/**
 * Проверяет видима ли достатаочная часть
 * плеера для того, чтобы запустить его или поставить на паузу
 */
export const autoPlayPause = (
  video: HTMLVideoElement,
  tracker: VASTTracker
) => {
  if (isVisible(video)) {
    tracker.setPaused(false);
    tracker.setDuration(1);
    video
      .play()
      .then(() => {
        if (!isImpressed) {
          tracker.trackImpression();
          isImpressed = true;
        }
        tracker.setPaused(false);
      })
      .catch((error) => {
        console.error(`Ошибка старта видео ${error}`);
      });
  } else {
    video.pause();
    tracker.setPaused(true);
  }
};
