import { getWitdhAndHeight } from "../../shared/players/get-width-and-height";
import App from "./App.svelte";
import { ASPECT_RATIO, MEDIA_QUERYS, PLAYER_CLASS } from "./constants";
import type { playerInitParamsOutstream } from "./Interfaces/player-init-params";
import { selectNetwork } from "./player/select-network";
import { fetchVast } from "./VAST/fetch-vast";

const containers: NodeListOf<HTMLElement> = document.querySelectorAll(
  `.${PLAYER_CLASS}`
);

const initPlayers = async () => {
  for (const container of Array.from(containers)) {
    const set = container.dataset;

    const config: playerInitParamsOutstream = {
      media_querys: (set.media_querys ||
        MEDIA_QUERYS) as playerInitParamsOutstream["media_querys"],
      aspect_ratio: parseInt(String(set.aspect_ratio)) || ASPECT_RATIO,
      looped: set.looped || "true",
      retrays: set.retrays || 2,
      userId: set.userId || "",
      siteId: set.siteId || "",
      subId: set.subId || "",
      prId: set.prId || "",
      sourceId: set.sourceId || "",
      fly: set.fly || "false",
      test: set.test || "false",
      lineColor: set.lineColor || "rgb(80, 79, 79)",
      link: set.link,
    };

    const playerLink = selectNetwork(config);
    /**
     * В зависимости от заданных пропорций и самого контейнера высчитывает
     * ширину и высоту плеера
     */
    const widthAndHeight = getWitdhAndHeight({
      aspect_ratio: config.aspect_ratio,
      parentNode: container,
    });

    // Only in TEST BUILD !

    /* if (config.link !== void 0) {
			return new Player(config.link, container, config);
		  } */

    // --------------------

    const initPlayerInstans = async (currentRetray: number) => {
      if (currentRetray > parseInt(String(config.retrays))) return;
      console.log("init player " + currentRetray);

      try {
        const currentAds = await fetchVast({
          url: playerLink,
          widthAndHeight,
        });
        /** Уменьшаем колличество попыток перезапуска на currentRetray */
        const mutateConfig = {
          ...config,
          retrays: parseInt(String(config.retrays)) - currentRetray,
        };

        /** Запустить на каждый таргет инстанс плеера */
        new App({
          target: container,
          props: {
            config: mutateConfig,
            currentAds: currentAds,
            container: container,
            widthAndHeight,
            url: playerLink,
          },
        });
        return true;
      } catch (error) {
        console.log(error);
        setTimeout(() => initPlayerInstans(currentRetray + 1), 1000);
      }
    };

    initPlayerInstans(0);
  }
};

console.log("Version 1.0.1");
initPlayers();
