import { PROD_BUILD_URL, TEST_BUILD_URL } from "../constants";
import type { playerInitParamsOutstream } from "../Interfaces/player-init-params";

/**
 * При наличии параметра data-sourceId плеер будет работать с новой
 * сеткой digital frontier
 * При этом параметры старой сети, кроме subId, trafbucks будут игнорироваться
 */
export const selectNetwork = (config: playerInitParamsOutstream) => {
  console.log(config.test);

  return config.link || config.test === "true"
    ? `${TEST_BUILD_URL}/${config.sourceId}${
        config.subId === "" ? "" : "?subid=" + config.subId
      }`
    : `${PROD_BUILD_URL}/${config.sourceId}${
        config.subId === "" ? "" : "?subid=" + config.subId
      }`;
  /*   return `${PROD_BUILD_URL}/${config.sourceId}${
    config.subId === "" ? "" : "?subid=" + config.subId
  }`; */
};
