<script lang="ts">
  import { onDestroy, onMount } from "svelte";
  import type { VastCreativeLinear } from "vast-client";
  import { AdsError } from "../../shared/errors/ads-error";
  import { getWitdhAndHeight } from "../../shared/players/get-width-and-height";
  import { log } from "../../shared/support/log";
  import type { CurrentAds } from "../../shared/types/vast/current-ads";
  import type { videoSourceT } from "../../shared/types/video-source";
  import { ASPECT_RATIO } from "./constants";
  import type { playerInitParamsOutstream } from "./Interfaces/player-init-params";
  import { autoPlayPause } from "./player/auto-play-pause";
  import { compliteStore } from "./store/complite-store";
  import { duration } from "./store/duration";
  import { errorStore } from "./store/error-store";
  import { impressionStore } from "./store/impression-store";
  import { currentTime } from "./store/time-store";
  import { videoStore } from "./store/video-store";

  interface videoPropsI {
    isMuted: boolean;
    videoSources: videoSourceT[];
    currentAds: CurrentAds;
    looped: string;
    /** DOM ref to reinit button*/
    reinitButton: HTMLButtonElement;
    config: playerInitParamsOutstream;
  }
  export let props: videoPropsI = {
    isMuted: true,
    videoSources: [{ src: "", type: "video/mp4", media: "", poster: "" }],
    currentAds: {} as CurrentAds,
    looped: "true",
    reinitButton: document.createElement("button"),
    config: {} as playerInitParamsOutstream,
  };

  /* DOM REFS */
  /**
   * Для доступа к DOM элементам из кода
   */

  let videoHeight = "";

  const playActionOnce = () => props.currentAds.tracker.trackImpression();
  const playAction = () => {
    props.currentAds.tracker.setPaused(false);
  };
  const errorAction = (error: unknown) => {
    if (error instanceof Error) {
      log(`Error in source video - ${error?.message || error}`);
    }
    errorStore.set(new AdsError());
  };

  const pauseAction = () => props.currentAds.tracker.setPaused(true);

  const endedAction = () => {
    isComplited ? "" : props.currentAds.tracker.complete();
    isComplited = true;
    log("PLAYER ended");
    /**
     * Показываете кнопку перезапуска и
     * возвращает видео в стартовую позицию
     */
    const reinitAction = () => {
      props.reinitButton.setAttribute("style", "display:block !important");
      $videoStore.setAttribute("preload", "none");
      $videoStore.load();
    };
    /**
     * Зацикленные видео начинают играться повторно а
     * те от тех что нет отписываются события
     */
    props.looped === "true" ? $videoStore.play() : reinitAction();
  };
  const volumeAction = (e: Event) => {
    props.currentAds.tracker.setMuted((<HTMLVideoElement>e.target).muted);
  };
  const clickAction = () => {
    props.currentAds.tracker.click(
      (props.currentAds.ads.creatives[0] as VastCreativeLinear)
        ?.videoClickThroughURLTemplate?.url
    );
  };

  /**
   * Обычно для получения значения currentTime,
   * используют отслеживание событий timeupdate.
   * Но эти события запускаются слишком редко,
   * что приводит к дёрганому интерфейсу.
   * Svelte работает лучше — он проверяет currentTime,
   * используя requestAnimationFrame.
   */
  let unsubscribeTimeUp = currentTime.subscribe((progressTime) => {
    props.currentAds.tracker.setProgress(progressTime);
  });

  const loaded = () => {
    duration.set($videoStore.duration);
    // Для корректного трекинга квартилей сказать про продолжительность ролика
    props.currentAds.tracker.setDuration($duration);
    props.currentAds.tracker.load();
  };

  const otherAdInteraction = () => {
    props.currentAds.tracker.otherAdInteraction();
  };

  onMount(() => {
    const tracker = props.currentAds.tracker;
    autoPlayPause($videoStore, tracker);
    /**
     * Навесить действие по клику на кнопку перезапуска
     */
    props.reinitButton?.addEventListener("click", () => {
      $videoStore.setAttribute("preload", "none");
      $videoStore.load();
    });
    tracker.on("clickthrough", (url: string) => {
      window.open(url);
    });

    /**
     * EVENTS on video actions
     */

    // only for logs in dev mode

    tracker.on("creativeView", () => {
      impressionStore.set(true);
      log(`creativeView`);
    });
    tracker.on("complete", () => {
      compliteStore.set(true);
    });
    /**
     * Навешивать обработчик только на последний source.
     * Ведь ошибка в других source ни о чем не говорит, последний
     * используется как fallback
     * */
    const lastSource: HTMLSourceElement | null =
      $videoStore.querySelector("source:last-child");
    lastSource?.addEventListener("error", errorAction);
  });

  onDestroy(() => {
    unsubscribeTimeUp();

    $videoStore
      .querySelector("source:last-child")
      ?.removeEventListener("error", errorAction);
  });

  let isComplited = false;

  /**
   * Проставляет высоту видео тега и стратует или стопает
   * видео в зависимости от его видимости
   */
  const setVideoHeightAndPlayPause = () => {
    videoHeight =
      `${
        getWitdhAndHeight({
          aspect_ratio: props.config.aspect_ratio || ASPECT_RATIO,
          parentNode: $videoStore,
        })[1]
      }px` || "100%";

    props.looped === "false" && $compliteStore
      ? ""
      : autoPlayPause($videoStore, props.currentAds.tracker);
  };
</script>

<svelte:window
  on:scroll="{setVideoHeightAndPlayPause}"
  on:resize="{setVideoHeightAndPlayPause}"
/>

<video
  bind:this="{$videoStore}"
  class="df--video"
  muted="{props.isMuted}"
  preload="metadata"
  playsinline
  x-webkit-airplay="allow"
  webkit-playsinline
  controls="{false}"
  autoplay="{true}"
  style="height: {videoHeight};"
  on:canplay="{loaded}"
  on:play="{playAction}"
  on:play|once="{playActionOnce}"
  on:pause="{pauseAction}"
  on:error="{errorAction}"
  on:click="{clickAction}"
  on:volumechange="{volumeAction}"
  bind:currentTime="{$currentTime}"
  on:mouseover="{otherAdInteraction}"
  on:ended="{endedAction}"
  poster="data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH/C05FVFNDQVBFMi4wAwEAAAAh+QQEAAAAACwAAAAAAQABAAACAkQBADs="
>
  {#each props.videoSources as source}
    <source
      src="{source.src}"
      type="{source.type}"
      media="{navigator.userAgent.search(/Edge/) > 0 ? '' : source.media}"
    />{/each}
</video>

<style>
  .df--video {
    width: 100% !important;
    max-height: 100%;
    object-fit: cover;
    grid-area: 1/ -1;
  }
  .df--video:hover {
    cursor: pointer;
  }
</style>
